@import url(//fonts.googleapis.com/css?family=Open+Sans:300,600);

body {
    font-family: 'Open Sans', sans-serif !important;
}

/* typography */
.heading-primary {
    font-weight: 300;
    text-transform: capitalize;
    margin-bottom: 20px;
}

/* icon */
.icon {
    height: 20;
}

.btn-card {
    color: #fff;
    border: none;
    padding: 10px;
    padding-left: 20px;
    border-radius: 3px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-image: -webkit-gradient(linear , left top, right top , color-stop(0, var(--indigo)), color-stop(50%, var(--indigo)), to(var(--blue)));
    background-image: -webkit-linear-gradient(left , var(--indigo) 0, var(--indigo) 50%, var(--blue));
    background-image: -o-linear-gradient(left , var(--indigo) 0, var(--indigo) 50%, var(--blue));
    background-image: linear-gradient(to right , var(--indigo) 0, var(--indigo) 50%, var(--blue));
}
.btn-card:hover {
    color: #fff;
    text-decoration: none;
}

/* input  */
#custom-search-input{
    padding: 14px;
    border: solid 1px #e4e4e4;
    border-radius: 10px;
    background-image: -webkit-gradient(linear, left top, right top, color-stop(0, var(--indigo)),color-stop(50%, var(--indigo)), color-stop(50%, var(--blue)));
    background-image: -webkit-linear-gradient(left, var(--indigo) 0,var(--indigo) 50%, var(--blue) 50%);
    background-image: -o-linear-gradient(left, var(--indigo) 0,var(--indigo) 50%, var(--blue) 50%);
    background-image: linear-gradient(to right, var(--indigo) 0,var(--indigo) 50%, var(--blue) 50%);
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

#custom-search-input input{
    border: 0;
    border-radius:6px;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-border-radius:6px;
    -moz-border-radius:6px;
    -ms-border-radius:6px;
    -o-border-radius:6px;
}


#custom-search-input button{
    background: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    border: 0;
    color: #fff;
}

#custom-search-input button:hover{
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}

#custom-search-input .fa-search{
    font-size: 23px;
}

/* gradient classses */
.gd {
    background-image: -webkit-gradient(linear, left top, right top, from(var(--blue)), to(var(--indigo)));
    background-image: -webkit-linear-gradient(left, var(--blue), var(--indigo));
    background-image: -o-linear-gradient(left, var(--blue), var(--indigo));
    background-image: linear-gradient(to right, var(--blue), var(--indigo))
}




